import { NextResponse } from 'next/server';
import { FirebaseApi } from '@/api/firebase';

export async function GET() {
  try {
    console.log('Testing blog API...');
    
    // Test getBlog function
    const blogResult = await FirebaseApi.getBlog();
    console.log('Blog API result:', blogResult);
    
    // Test getBlogBySlug function with a test slug
    const blogBySlugResult = await FirebaseApi.getBlogBySlug('test-blog');
    console.log('Blog by slug result:', blogBySlugResult);
    
    return NextResponse.json({
      success: true,
      blogCount: blogResult.ok ? blogResult.data.length : 0,
      blogs: blogResult.ok ? blogResult.data : [],
      blogBySlug: blogBySlugResult.ok ? blogBySlugResult.data : null,
    });
  } catch (error) {
    console.error('Test blog API error:', error);
    return NextResponse.json({
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error'
    }, { status: 500 });
  }
}